section {
  width: 335px;
  height: 352px;
  color: #fff;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  margin-left: 20px;
  margin-right: 20px;
  font-family: DM Sans, sans-serif;
  font-size: 22px;
  font-weight: 700;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

section.active {
  height: auto;
}

.funds__list {
  width: 159px;
  height: 188px;
  margin-left: 40px;
  padding: 0;
  transition: heigh 3s;
  overflow: hidden;
}

.funds__list.active {
  height: auto;
}

.slider {
  background-color: rgba(255, 0, 0, 0);
  border-color: rgba(255, 0, 0, 0);
  margin: 16px auto 0;
  display: block;
}

@media screen and (min-width: 768px) {
  .section {
    width: 356px;
    height: 474px;
    margin-left: 401px;
  }

  .container {
    width: 100%;
    max-width: 768px;
  }

  .funds__list {
    height: 292px;
  }

  .support_Ukraine {
    padding-right: 87px;
  }

  .slider {
    margin: 24px auto 0;
  }
}

@media screen and (min-width: 1440px) {
  .section {
    width: 356px;
    height: 474px;
    margin-left: 24px;
  }

  .container {
    max-width: 1252px;
  }
}

.fund__li {
  height: 31px;
  margin-top: 20px;
  list-style: none;
}

.fund__li:first-child {
  margin-top: 0;
}

.fund_numder {
  color: #fff;
  margin-right: 16px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.support_Ukraine {
  padding-top: 24px;
  padding-left: 40px;
  padding-right: 102px;
  display: inline;
}

.funds {
  margin-right: 0;
}

.fund__icon {
  flex-flow: row;
  justify-content: flex-start;
  align-items: center;
  text-decoration: none;
  display: flex;
}

